//è·å–æœ¬åœ°æ•°æ®
function getData(dataName) {
  // localStorgeä¸ºwindowå†…ç½®å±æ€§ï¼Œç”¨äºåœ¨æµè§ˆå™¨å­˜å‚¨æœ¬åœ°ä¿¡æ¯
  // è¯¥æ•°æ®é™¤éæ‰‹åŠ¨åˆ é™¤ï¼Œå¦åˆ™ä¼šä¸€ç›´å­˜åœ¨
  // let data = localStorage.getItem("todoList") //å–å€¼
  let data = localStorage.getItem(dataName); //å–å€¼
  if (data !== null) {
    //ES6å°†å˜é‡ä½œä¸ºé”®â†’[å˜é‡å]
    // return JSON.parse(data).[dataName] //å°†æ•°æ®è½¬æ¢ä¸ºjson
    return JSON.parse(data);
  } else {
    return [];
  }
}

//ä¿å­˜æœ¬åœ°æ•°æ®
function saveData(data, dataName) {
  //JSON.stringify()å°†jsonæ•°æ®è½¬æ¢ä¸ºå­—ç¬¦ä¸²
  localStorage.setItem(dataName, JSON.stringify(data)); //è®¾å€¼
}

//åˆ é™¤æ•°æ®
function deleteData(index, dataName) {
  //è·å–æœ¬åœ°æ•°æ®
  let data = getData(dataName);
  // let index = index //è·å–ç´¢å¼•å·
  //æ”¹æœ¬åœ°æ•°æ®ï¼ˆåˆ é™¤æ•°æ®ï¼‰
  data.splice(index, 1);
  //ä¿å­˜æœ¬åœ°æ•°æ®
  saveData(data);
}

