//è·å–å½“å‰æ—¶é—´å‡½æ•°
let getDateStr = function() {
  var dd = new Date();
  var y = dd.getFullYear();
  var m = dd.getMonth() + 1; //è·å–å½“å‰æœˆä»½çš„æ—¥æœŸ
  if (m < 10) {
    m = '0' + m;
  }
  var d = dd.getDate();
  if (d < 10) {
    d = '0' + d;
  }
  return y + '-' + m + '-' + d;
};

export { getData, saveData, deleteData, getDateStr };
